/**
 *  "Range"    HTML-  (progress bar).
 *    "subView",     ()    MVP.
 */
export class Range{
  /**
   * HTML- .
   * @type {HTMLElement}
   * @private
   */
  private _htmlElement: HTMLElement;

  /**
   *  HTML-  0  100  .
   * @type {number | null}
   * @private
   */
  private _width: number | null = 100;

  /**
   *  HTML-  0  100  .
   * @type {number | null }
   * @private
   */
  private _height: number | null = 100;

  /**
   *   HTML-.
   * @type {string}
   * @private
   */
  private _className: string;

  /**
   *
   * @param className
   */
  private _rangeType: RangeType = RangeType.HIDDEN;

  /**
   * @constructor
   * @this   {Range}
   * @param  {AbacusClasses} classes    .
   * @example new Handle({
   *  range: 'abacus__range'
   * });
   */
  constructor(classes?: AbacusClasses){
    this._htmlElement = document.createElement('span');
    this._className = classes?.range ? classes.range : 'abacus__range';
    this._htmlElement.classList.add(this._className);
  }


  /**
   *   HTML- (_htmlElement).
   */
  public get width() : number | null {
    return this._width;
  }

  /**
   *   HTML- (_htmlElement).
   * @param {number | null} width     0  100.
   */
  public set width(width : number | null) {
    if( width === null ){
      this._width = width;
      this._htmlElement.style.width = '';
    }
    else{
      if( width < 0 ) width = 0;
      if( width > 100 ) width = 100;

      this._width = width;
      this._htmlElement.style.width = width + '%';
    }
  }


  /**
   *   HTML- (_htmlElement).
   */
  public get height() : number | null {
    return this._height;
  }

  /**
   *   HTML- (_htmlElement).
   * @param {number | null} height     0  100.
   */
  public set height(height : number | null) {
    if( height === null ){
      this._height = height;
      this._htmlElement.style.height = '';
    }
    else{
      if( height < 0 ) height = 0;
      if( height > 100 ) height = 100;

      this._height = height;
      this._htmlElement.style.height = height + '%';
    }
  }


  /**
   *    HTML-.
   */
  public get htmlElement() : HTMLElement {
    return this._htmlElement;
  }


  /**
   *    HTML-.
   */
  public get className() : string {
    return this._className;
  }


  /**
   *    HTML-.     HTML-,     .
   * @param {string} value  .
   */
  public set className(value : string) {
    this._htmlElement.classList.remove(this._className);
    this._htmlElement.classList.add(value);
    this._className = value;
  }


  /**
   *
   */
  public get rangeType() : string {
    return this._rangeType;
  }

  /**
   *
   */
  public set rangeType(value : string) {
    if( value !== 'hidden' && value !== 'min' && value !== 'max' && value !== 'between' ){
      value = 'hidden';
    }
    this._rangeType = value as RangeType;
  }

}

enum RangeType {
  HIDDEN = 'hidden',
  MIN = 'min',
  MAX = 'max',
  BETWEEN = 'between'
}